
.wrapper {
    position: relative;

    height: 100vh;

    display: flex;
    flex-wrap: nowrap;
    overflow-x: hidden;
}
.slide {
    width: 100%;
    height: 100%;
    flex-shrink: 0;
    background-image: center;
    background-size: cover;   
    transition: 1000ms all ease-in-out;
}
.children-wrapper{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: #fff;
    background: rgb(41, 41, 41);
    padding-left: 20px;
    padding-right: 20px;
    opacity: 0.5;
}